<template>
  <div id="app" :v-if="flag">
    <!--<router-view/>-->
    <router-view></router-view>
    <!--<Index></Index>-->
  </div>
</template>

<script>
  import Index from '@/components/Index'

  export default {
    name: 'App',
    components: {
      Index,
    },
    data: function () {
      return {
        flag: false,
      }
    },
    mounted: function () {
      this.flag = !this.flag;
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }

</style>



